/* Navigation */
body {
    transition: background-color .5s;
}

.nav-items {
    position: absolute;
    height: 10% !important;
    right: 10% !important;
    top: 5% !important;
}

.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #232340;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
}

.sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s
}

.sidenav a:hover, .offcanvas a:focus{
    color: #fafafa;
}

.closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px !important;
    margin-left: 50px;
}

#main {
    transition: margin-left .5s;
    padding: 16px;
}

.nav-link {
    color: #232340 !important;
    font-size: 18px !important;
}

.navbar-brand h1 {
    color: #232340;
    font-weight: 700 !important;
    margin: 0;
}

.navbar-brand img {
    width: 48px !important;
    height: 48px !important;
    margin-right: 10px;
}

.navbar-brand span {
    color: #5468e7 !important;
    font-size: 72px;
    margin-left: -5px !important;
}

.nav-link:hover {
    color: #5468e7 !important;
}

.btn-outline-dark {
    color: #5468e7 !important;
    border-color: #5468e7 !important;
}

.btn-outline-dark:hover {
    border-color: #232340 !important;
    color: #232340 !important;
    background-color: transparent !important;
}

#main span {
    display: none;
}

@media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
}

@media (max-width: 992px) {
    #main {
        padding: 0;
    }
    #mySidenav {
        -webkit-filter: drop-shadow(3px 3px 6px #555);
                filter: drop-shadow(3px 3px 6px #555);
        z-index: 9999999;
    }
    #main span {
        cursor: pointer;
        display: block;
        position: absolute;
        font: size 36px;
        top: 30px !important;
        right: 10% !important;
    } 
    .nav-items {
        display: none !important;
    }
    #mySidenav .nav-link {
        text-align: center;
        display: block;
        color: #95c4ec !important;
        padding: 10px 0 !important;
        width: 300px !important;
    }
    #mySidenav .btn-outline-dark {
        margin: auto;
        width: 95% !important;
    }
    #mySidenav .btn-outline-dark {
        color: #95c4ec !important;
        border-color: #95c4ec !important;
        margin-top: 10px;
    }
    #mySidenav .btn-outline-dark:hover {
        border: none !important;
        background-color: #5468e7 !important;
        color: #232340 !important;
    }
}
/* ABOUT */

.about-container {
    -webkit-filter: drop-shadow(3px 3px 6px #555);
            filter: drop-shadow(3px 3px 6px #555);
    padding: 5% 0 !important;
    padding-top: 2%;
    margin: 0 auto !important;
    margin-bottom: 5% !important;
}

.about-inner {
    overflow: hidden;
    padding: 0;
}

.about-bg {
    position: absolute;
    background-color: #232340;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100% !important;
    height: 110%;
    padding: 0 !important;
    transform: skewY(-5deg);
    transform-origin: top left;
    border-bottom-left-radius: 50% 20%;
    border-bottom-right-radius: 50% 20%;
}

.about-content {
    width: 80% !important;
    margin: 0 auto !important;
    padding: 0 !important;
    padding-bottom: 5% !important;
}

.about-right {
    position: relative;
    right: 0 !important;
    padding: 0 !important;
}

.about-right b {
    color: #b5dacd;
}

.about-right h4 {
    color: #ffce79;
    font-size: 300 !important;
}

.about-right h2 {
    color: #fafafa;
    font-weight: 700 !important;
    font-size: 36px;
    margin-bottom: 5%;
}

.about-right p {
    font-family: 'Nanum Gothic', sans-serif !important;
    text-transform: none;
    text-transform: initial;
    color: #fafafa;
    font-size: 18px;
    margin-bottom: 5%;
}

.about-right ul {
    list-style: none !important;
    width: 50%;
    padding: 0;
    margin-right: 10px;
    margin: 3% 0;
}

.about-right li {
    cursor: pointer;
    color: #b5dacd !important;
    width: 90%;
    font-size: 16px;
    margin-bottom: 5px;
    padding: 5px 10px;
}

.about-right span {
    background-color: #fff;
    color: #5468e7;
    font-size: 12px;
    font-weight: 700 !important;
    margin-right: 10px;
    padding: 3px;
}

.about-left {
    display: flex;
    align-items: center;
    width: 100%;
}

.about-left img {
    border: 3px solid #b5dacd;
    width: 350px;
    height: 350px;
    margin-bottom: 40px !important;    
    margin-right: 50px !important;  
    border-radius: 10px; 
    transition: all 0.2s ease-out;
    z-index: 9999;
}

.in {
    width: 80%;
    margin-bottom: 200px;
  }
  
  .header {
    fill:url(#linear-gradient);
  }
  
  .footer {
    fill: url(#linear-gradient);
  }

.about-left img:hover {
    transform: translate(10px, 10px); 
}

.about-left div {
    position: absolute;
    border: 3px solid #b5dacd;
    border-radius: 10px; 
    width: 350px;
    height: 350px;
    margin-left: 20px !important;  
}

@media (max-width: 1050px) {
    .about-right ul {
        width: 50%;
    }
    .about-right li {
        width: 100%;
        font-size: 12px;
    }
    .about-right {
        margin: 5% auto !important;
    }
    .about-left {
        justify-content: center !important;
        margin: 5% auto;
    }
    .about-left div {
        margin-left: 0px !important;
    }
}

@media (max-width: 992px) {
    .about-container {
        margin-bottom: 20% !important;
    }
    .about-right ul {
        padding-left: 10px;
    }
}

@media (max-width: 768px) {
    .about-container {
        background-color: #232340;
        margin-bottom: 20% !important;
    }
    .about-bg {
        background-color: transparent;
        transform: skewY(0deg);
        border-bottom-left-radius: 0%;
        border-bottom-right-radius: 0%;
    }
    .about-content {
        padding-bottom: 0;
    }
    .about-left img {
        width: 300px;
        height: 300px;
    }    
    .about-left div {
        width: 300px;
        height: 300px;
    }   
}
