/* LOADING */
.progress-bar {
    background: #fafafa !important;
    border: 5px solid #5468e7 !important;
    width: 300px;
    height: 40px;
    border-radius: 50px;
}

.progress {
    box-sizing: border-box;
    background-color: #b5dacd !important;
    animation: loading 5s linear 2s infinite;
    animation-name: loading;
    width: 20%;
    height: 150% !important;
    border-radius: 50px;
}

.page {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-transform: uppercase;
  font-weight: normal;
  font-style: normal;
  background: #fafafa !important;
  color: #5468e7 !important;
  margin: 0;
  height: 100vh;
  font-weight: 300 !important;
  width: 100%;
  font-size: 36px !important;
}

@keyframes loading {
    0%   {width: 20%;}
    50%  {width: 50%;}
    100% {width: 100%;}
}
/* CONTACT */

.get-in-touch  h4 {
    color: #ffce79;
    font-weight: 300 !important;
    margin: 0 auto !important;
}

.get-in-touch  h2 {
    color: #232340;
    font-weight: 700 !important;
    font-size: 36px;
    margin-bottom: 5%;
}

.get-in-touch {
    text-align: center;
    margin: 0 auto !important;
    margin-bottom: 5% !important;
}

.contact-form .form-field {
    position: relative;
    text-align: left;
    margin: 32px 0;
}

.contact-form .input-text {
    display: block;
    border-bottom: 2px solid #5468e7;
    width: 100%;
    height: 36px;
    border-width: 0 0 2px 0 !important;
    font-size: 24px;
    line-height: 30px;
}

.contact-form .input-text:focus {
    outline: none;
}

.contact-form .input-text:focus + .label,
.contact-form .input-text.not-empty + .label {
    transform: translateY(-16px);
}

.contact-form .input-text:valid + .label {
    transform: translateY(-16px);
}

.contact-form .label {
    position: absolute;
    cursor: text;
    color: #5468e7 !important; 
    left: 20px;
    bottom: 24px;
    font-size: 24px;
    line-height: 30px;
    font-weight: 600;
    transition: transform .2s ease-in-out;
}

.js-input option {
    font-size: 12px !important;
}

.contact-form .submit-container {
    text-align: center !important;
    margin-bottom: 0;
}

.contact-form .submit-btn {
    cursor: pointer;
    text-transform: uppercase !important;
    background-color: #5468e7;
    box-shadow: 0px 4px 8px -3px #5468e7;
    color: #fafafa !important;
    font-weight: 300 !important;
    width:200px;
}

div.get-in-touch-content {
    margin: 0% auto !important;
    padding: 0 !important;
    margin-bottom: 5% !important;
    width: 100% !important;
}

.contact-form .submit-btn:hover {
    background-color: #c898e4;
    box-shadow: 0px 4px 8px -3px #c898e4;
}

#alert {
    font-family: 'Sacramento', cursive !important;
    text-align: center;
    text-transform: lowercase;
    display: none;
    font-weight: bold;
    background-color: #95c4ec;
    color:#232340;
    width: 20% !important;
    font-size: 24px;
    padding: 10px;
    margin: 5% auto !important;
    margin-top: 0 !important;
    z-index: 99999;
}

.contact-form {
    padding: 0 !important;
    margin: 0 auto !important;
}

@media (max-width: 992px) {
