/* NOT FOUND */
.notfound-container {
    margin: 13% auto;
}

.page-wrapper {
    display: flex;
    justify-content: center;
    position: relative;
    width: 246px;
    margin: 3% auto;
}

.disk-wrapper {
    overflow: hidden;
    position: relative;
    overflow: hidden;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 200px;
}

.disk-wrapper .light-left, .disk-wrapper .light-right {
    width: 25%;
    height: 30%;
    position: absolute;
    background-color: #615951;
    z-index: 20;
}

.disk-wrapper .light-left {
    top: 20px;
    left: 20px;
    transform: rotate(-45deg);
    -webkit-clip-path: polygon(0 0, 100% 0, 66% 100%, 40% 100%);
            clip-path: polygon(0 0, 100% 0, 66% 100%, 40% 100%);
}

.disk-wrapper .light-right {
    -webkit-clip-path: polygon(40% 0, 66% 0, 100% 100%, 0 100%);
            clip-path: polygon(40% 0, 66% 0, 100% 100%, 0 100%);
    right: 20px;
    bottom: 20px;
    transform: rotate(-45deg);
}

.disk {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: #050608;
    width: 200px;
    height: 200px;
    border-radius: 100%;
    animation: circle 2.5s infinite linear;
}

.disk .half-circle-top {
    top: 30px;
    border: 2px solid #615951 21;
    border-top-left-radius: 72px;
    border-top-right-radius: 72px;
    border-bottom: 0;
}

.disk .half-circle-bottom {
    bottom: 30px;
    border: 2px solid #615951 21;
    border-bottom-left-radius: 72px;
    border-bottom-right-radius: 72px;
    border-top: 0;
}

.disk .half-circle-bottom, .disk .half-circle-top {
    position: absolute;
    background-color: #050608;
    width: 140px;
    height: 70px;
}

.disk .separator {
    position: absolute;
    background-color: #050608;
    width: 100%;
    height: 58px;
    top: 36%;
}

.disk .inner-circle {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #5468e7;
    width: 90px;
    height: 90px;
    border-radius: 100%;
    z-index: 2;
}

.disk .inner-circle span {
    color: #95c4ec;
    font-weight: 900;
    font-size: 25px;
}

.disk .inner-circle .dot {
    background-color: black;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    margin: 5px;
}

.needle-wrapper {
    position: absolute;
    right: 0;
}

.needle-wrapper .base {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: #d2ccc6;
    width: 30px;
    height: 30px;
    border-radius: 3px;
}

.needle-wrapper .base:after {
    content: "";
    background-color: #615951;
    border-radius: 100%;
    width: 20px;
    height: 20px;
}

.needle-wrapper .lines {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    z-index: 4;
    top: -16px;
}

.needle-wrapper .lines div {
    background-color: #d2ccc6;
    border-radius: 3px;
}

.needle-wrapper .lines .vertical {
    width: 6px;
    height: 50px;
}

.needle-wrapper .lines .horizontal {
    position: absolute;
    width: 66px;
    height: 6px;
    left: -46px;
    bottom: -13px;
    transform: rotate(-20deg);
}

@keyframes circle {
    100% {
        transform: rotate(360deg);
	}
}

.error {
    font-family: 'Sacramento', cursive !important;
    text-transform: lowercase;
    text-align: center;
    background-color: #b5dacd;
    color: #232340;
    font-size: 28px;
    width: 50%;
    margin: 30px auto;
}

.error-goback {
    position: relative;
    cursor: pointer;
    left: 10%;
    font-size: 20px !important;
}

@media (max-width: 992px) {
    .notfound-container {
        margin: 25% auto;
    }
}
/* FONTS */
* {
    text-decoration: none !important;
    font-family: 'Ubuntu', sans-serif;
}

html {
    scroll-behavior:smooth;
}

#root {
    width: 100vw !important;
}

a,
button {
    cursor: pointer !important;
    outline: none;
}

a:active, 
a:focus {
    -moz-outline-style: none !important; 
    border: none !important;
    outline-style: none !important;
    outline: 0 !important;
}

input,
select,
textarea,
button {
    -moz-outline: none !important; 
    -moz-outline-style: none !important; 
    outline: none !important;
    outline-width: 0 !important;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    -moz-outline: none !important; 
    box-shadow: none!important;
    outline: none !important;
    outline-width: 0 !important;
    outline: 0 !important;
}

::selection {
    background-color: #b5dacd !important;
}
